<template>
  <div>
  <div >
      <div slot="content">
        <ul class="dialog-ul">
          <li class="formList allWidth">
            <label v-if="true" class="label-show" style="min-width:5em"><span>&nbsp;电话号码</span> </label>
            <inputs :isEmpty="false" :option="cstPhoneInputs" @getInput="getFrom" @enter="enter" ref="cstPhones.mobilePhone"></inputs>
            <button style="margin: 0;float: left; margin-left: 1em; height: 2.8em;min-width: 11em;" class="button-default"
                    @click="saveTbAdd">添加
            </button>
          </li>
        </ul>
        <table class="table-list" style="margin:0.5em 0.5em;width: calc(100% - 1em);">
          <tr class="table-header">
            <th >电话号码</th>
            <th style="width:8em">操作</th>
          </tr>
          <tr v-for='item in cstPhones.mobilePhones' v-if="item.optType=='add'" style="">
            <td>{{item.phone}}</td>
            <td  width="3em" @click="cstPhoneDel(item)" >
              <button class="list-operate" style="height: 22px;padding: 0;cursor: pointer;color:#69A4E7!important;margin-top: 0;"><i class="hasIcon ">
                <icon name="trash"></icon>
              </i>删除
              </button>
            </td>
          </tr>
        </table>
      </div>
      <div slot="footer">
        <button class="button-default" @click="saveCstPhones">确定</button>
        <button class="button-default cancel-button" @click="closePhoneWin">关闭</button>
      </div>
  </div>
  <dialogs type="alert" ref="alerts"></dialogs>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        cstPhoneInputs: {
          key: 'cstPhones.mobilePhone',
          width: 'calc(100% - 18em)!important',
          type: 'normalInput',
          length: 11,
          regular: 'phone',
          errorMsg: '联系电话：11位手机号',
          limit: 'number'
        },
        //客户电话信息框
        cstPhones: {
          mobilePhone: "", //电话号码
          customerNo: "",
          mobilePhones: []  //state 1 标识数据库数据 0标识新增数据
        }, //客户资料信息
      }
    },
    props: ['customerInfo', "optType"],
    methods: {
      enter: function (val) {
         this.getOption(val, this);
         this.saveTbAdd()
      },
      //#region 客户电话号码函数
      //保存电话号码
      saveTbAdd: function () {
        let that = this;
        if (!this.regularTestForVal({
            val: that.cstPhones.mobilePhone,           // 判断的值；
            regex: "phone",
            msg:  '电话号码格式有误'      // 正则
          })) {
       
        //  this.$refs['cstPhones.mobilePhone'].resetSearch('');

          return;
        }
     
        if (that.cstPhones.mobilePhone != '' && typeof that.cstPhones.mobilePhone != 'undefined') {
          let count = 0;
          for (let i = 0; i < that.cstPhones.mobilePhones.length; i++) {
             if(that.cstPhones.mobilePhones[i].optType == 'add'){
                count = count + 1;
             }
          }
          if (count > 9) {
             that.$refs.alerts.show({
               msg: '最多只能添加10个电话号码'
             });
          }  else {
            let isExist = false;
            //判断加入的电话号码是否存在
            for (let i = 0; i < that.cstPhones.mobilePhones.length; i++) {
              if (that.cstPhones.mobilePhones[i].phone == that.cstPhones.mobilePhone) {
                if(that.cstPhones.mobilePhones[i].optType == 'add') {
                   that.$refs.alerts.show({
                      msg: '已有该号码，请勿重复添加'
                  });
                }
                that.cstPhones.mobilePhones[i].optType = "add";
                isExist = true;
                break;
              }
            }
            if (!isExist) {
              that.cstPhones.mobilePhones.push({phone: that.cstPhones.mobilePhone, state: 0, optType: "add"});
            }
          }
          that.$refs['cstPhones.mobilePhone'].resetSearch('');
         }
      },
      //删除表格中的电话号码
      cstPhoneDel: function (item) {
        let that = this;
        if (item.state == 0) {
          for (let i = 0; i < that.cstPhones.mobilePhones.length; i++) {
            if (that.cstPhones.mobilePhones[i].phone == item.phone) {
              that.cstPhones.mobilePhones.splice(i, 1); //删除当前这项
              break;
            }
          }
        }
        else {
//          that.$refs.confirms.show(
//            {
//              msg: '电话号码删除后微信将自动解绑,是否继续？',
//              action: {
//                confirm: function () {
          item.optType = "delete";
//                }
//              }
//            });
        }
      },
      //保存电话号码
      saveCstPhones: function () {
        let that = this;
        if (that.cstPhones.mobilePhones.length == 0) {
          that.$refs.alerts.show({
            msg: '请至少添加一个号码'
          });
        } else {
          that.$emit('submit',that.cstPhones.mobilePhones);
        }
      },
      //加载电话信息
      loadPhones: function () {

        this.cstPhones.mobilePhones = [];
        let state = this.optType == "add" ? 0 : 1;
        if (this.customerInfo.mobilePhones && this.customerInfo.mobilePhones != '') {
          let attr = this.customerInfo.mobilePhones.split(',');
          for (let i = 0; i < attr.length; i++) {
            this.cstPhones.mobilePhones.push({phone: attr[i], state: state, optType: "add"});
          }
        }
      },

      closePhoneWin:function(){
        this.$refs['cstPhones.mobilePhone'].resetSearch('');
        this.$emit('closeWin');
      },
      //#endregion

      getFrom: function (val) {
        this.getOption(val, this)
      }
    },

    mounted: function () {
      this.loadPhones();
      //this.$refs['cstPhones.mobilePhone'].resetSearch('');
    },
     beforeUpdate: function () {
     
    },
    watch: {
      'customerInfo': {
        handler (val){
          this.loadPhones();
        
        }
      }
    }

  }
</script>



