<template >
  <div class="myDialog" >
    <dialogs type="customer" width="45em" v-if="showDialog" ref="uploadFile" overflow="auto" maxHeight="25em"  isPadding="true">
      <div slot="header">
        上传文件
      </div>
      <div slot="content">
        <input  name="fileName" multiple type="file" data-show-caption="true" data-min-file-count="1" id="upfilectrl"/>
        <p class="help-block"><span>支持 {{getAllowExtensions()}} 格式，大小不超过 {{upLoadAttrite.fileSize/1000}} M </span>
          <a class="templatedown" v-if="upLoadAttrite.templateUrl!==''" :href="upLoadAttrite.templateUrl"
          target="_blank">
             <i class="hasIcon">
                <icon name="download" scale="1.2"></icon>
             </i> 模板下载
           </a>
        </p>
      </div>
    </dialogs>
    <dialogs type="alert" ref="alerts"></dialogs>
  </div>
</template>
<script>

  // import   '../../../dist/fileInput/fileinput.js'
  export default{
    data () {
      return {
          cursor: 'default',
          uploadResolute: false,
          showDialog: false,
          styleParams: '',
        //上传属性
        upLoadAttrite: {
          extensions: ['jpg', 'txt'], //上传文件的后缀
          fileSize: 2000,//文件的大小
          reqParam: {},
          templateUrl: '1',
          url: ''
        },
        //是否即时上传
        ForthwithLoad: false,
        //上传成功后的服务端返回的数据
        upLoadedData: {
          fileName: '文件名称',
          saveName: '存储临时目录的名称',
          importId: '导入的UUID',
          importTime: '导入的时间'
        }
      }
    },
    props: ['upLoadAttrParam', 'ForthwithLoadParam'],
    methods: {
        closeDialog: function () {
            this.showDialog = false;
        },
        show: function () {
            let that = this;
            that.showDialog = true;
            setTimeout(function () {
                that.$refs.uploadFile.show({
                  hasClose: true,
                  action: {
                      cancel: function () {
                          that.close()
                      }
                  }
                });
                that.initFileInput();
                $('.fileinput-remove').trigger("click");
            }, 0)
        },
      //初始化上传控件
      initFileInput: function (uploadUrl) {
        let that = this;
        let control = $('#upfilectrl');
        let url = this.getPath(this.upLoadAttrite.url);
        control.fileinput({
          language: 'en', //设置语言
          uploadUrl: url,  //上传地址
          uploadExtraData: this.upLoadAttrite.reqParam,
          showUpload: true,
          uploadAsync: true, //默认异步上传
          showClose: false,//是否显示关闭按钮
          allowedFileExtensions: this.upLoadAttrite.extensions,//接收的文件后缀['jpg', 'gif', 'png']
          maxFileSize: this.upLoadAttrite.fileSize,//单位为kb，如果为0表示不限制文件大小
          minFileCount: 1,//最小数量上传
          maxFileCount: 10,//表示允许同时上传的最大文件个数
          validateInitialCount: true, //是否验证上传文件的数量 data-min-file-count
          msgFilesTooMany: "选择上传的文件数量({n}) 超过允许的最大数值{m}！", //上传文件提示信息
//          showUpload: !this.ForthwithLoad, //是否显示上传按钮
//          showCaption: true,//是否显示标题
//          showCancel: true, //是否显示取消按钮
//          browseClass: "btn btn-primary", //浏览按钮样式
//          showRemove: false, //是否移除按钮
//          dropZoneEnabled: true, //是否显示拖拽区域
//          elCaptionText: '上传', //设置标题栏提示信息
//          allowedPreviewTypes: ['image'], //允许预览的文件
//          previewFileType: ['image'], //预览文件类型,内置['image', 'html', 'text', 'video', 'audio', 'flash', 'object',‘other‘]等格式
//           allowedFileTypes: ['image'],//接收的文件后缀 ['image', 'html', 'text', 'video', 'audio', 'flash', 'object',‘other‘]等格式
//          deleteUrl: '',  //删除路径
//          deleteExtraData: {},        //删除提供的参数
//          initialPreviewShowDelete: false,
//          previewFileIcon: "<i class='glyphicon glyphicon-file'></i>", //预览文件的图标

        }).on("filebatchselected", function (event, files) {
          //选择文件后处理事件

          if (files.length > 0 && that.ForthwithLoad) {
            // 执行文件上传
            $(this).fileinput("upload");
          }
        }).on("fileclear", function (event, data, msg) {
          //点击浏览框右上角X 清空文件前响应事件

        }).on("fileuploaded", function (event, data) {
          //上传完成回调函数
          let resdata = data.response;
          if (resdata.code === 302) {
              that.$router.push({path: '/'})
          } else {
              if (resdata.code === 1) {
                  that.upLoadedData = resdata.data;
                //  console.log(    that.upLoadedData)
                  //$("#myModal").find('.fileinput-remove').trigger("click");
                  //关闭上传
                  //  that.showDialog = false;
                  //调用上传后的事件
                  that.closeDialog();
                  that.$emit('upLoadFileFunc');
              }
              else {
                  //输出信息
                  that.$refs.alerts.show({msg: resdata.message});
              }
          }
        }).on("filecleared", function (event, data, msg) {
          //点击浏览框右上角X 清空文件后响应事件

        }).on('fileerror', function (event, data, msg) {
          let span = $('<span>');
          //异步上传错误结果处理
          that.$parent.msgShow(span.html(msg).text());
        });

      },
      //获取扩展名
      getAllowExtensions: function () {
        return this.upLoadAttrite.extensions.join(',');
      },
      close: function () {
        this.showDialog = false;
        this.$refs.uploadFile.close();
      }
    },
    components: {},
    mounted: function () {
      //初始化上传属性参数
      if (this.upLoadAttrParam !== null) {
        for (let key in this.upLoadAttrite) {
          if (this.upLoadAttrParam[key] !== null) {
            this.upLoadAttrite[key] = this.upLoadAttrParam[key];
          }
        }
      }
      //初始化上传模式
      if (this.ForthwithLoadParam !== null) {
        this.ForthwithLoad = this.ForthwithLoadParam;
      }
      //初始化上传控件

    },
    watch: {}
  }
</script>
<style scoped="scoped" lang="scss">

  @import '../../../dist/fileInput/fileinput.min.css';
  .uploadWin {
    position: absolute;
    left: 30%;
    top: 20%;
    background-color: #fff;
    width: 400px;
    border: 1px solid #f1f1f1;
  }
  .formBackIndex {
    z-index: 19
  }
  .formIndex {
    z-index: 20
  }
  .templatedown {
    color: #69A4E7;
    float: right;
    text-decoration: underline;
    i.hasIcon{
       margin-top: 0.2em;
       margin-right: 0.8em;
    }
  }
  .templatedown:hover{
    color: white
  }
  .myDialogs {

  }
</style>



