    submitDelWx: function submitDelWx(idAttr) {
      var that = this;
      that.$refs.confirms.show({
        msg: '确定解绑微信？',
        action: {
          confirm: function confirm() {
            that.getRequest({
              url: 'appPay/unbindWeChat',
              param: { ids: idAttr.join() },
              method: 'post',
              success: function success(data) {
                if (data.code == 1) {
                  that.seachWxBind();
                }
                that.alerts.show({
                  msg: data.message
                });
              },
              error: function error(data) {
                that.alerts.show({
                  msg: data.message
                });
              }
            }, true);
          }
        }
      });
    },

    seachWxBind: function seachWxBind() {
      var that = this;
      that.getRequest({
        url: '/customerInfo/selWXBind',
        param: { customerNo: this.customerInfo.customerNo, cno: "" },
        method: 'post',
        success: function success(data) {

          that.wxBinds = [];
          if (data.data && data.data.length > 0) {
            var Data = data.data;
            var len = Data.length;
            for (var i = 0; i < len; i++) {
              that.wxBinds.push(Data[i]);
              that.wxBinds[i]['isSel'] = false;
            }
          }
        },
        error: function error(data) {
          that.alerts.show({
            msg: data.message
          });
        }
      }, true);
    },

