<template>
    <div class="list-content noBorder"  :style="{width: width, padding: 0,background: 'transparent'}">
        <table class="form-list table-list" :style="{width: width, margin: 0, 'font-size': '0.875em'}">
            <tr class="table-header">
                <th></th>
                <th>第一套电价</th>
                <th>第二套电价</th>
            </tr>
            <tr v-for="(value, $indexs) in priceDatas" >
                <td>{{value.itemName1}}</td>
                <td>{{value.dataValue1}}</td>
                <td>{{value.dataValue2}}</td>
            </tr>
        </table>
    </div>
</template>
<script>
    export default{
        data () {
            return {
                priceDatas: ''
            }
        },
        props: {
            priceData: {
                type: Array,
                'default' () {
                    return  [
                        {
                            itemCode1:  '1',
                            itemName1: '尖电价',
                            dataValue1: 'XX',
                            itemCode2: '2',
                            itemName2:  '尖电价',
                            dataValue2:  'XX',
                        }
                    ]
                }
            },
            pricePlan: {
                type: String
            },
            width: {
                type: String,
                'default' : '100%'
            }
        },
        methods: {
            getTableData: function () {
               let that = this;
               let param = {};
               param['priceSolsCode'] = that.pricePlan;
               that.getRequest({
                   url: 'price/queryData',
                   method: 'post',
                   param: param,
                   success: function (data) {
                       that.priceDatas = data.price.parameters
                   }
               })
            }
        },
        mounted: function () {
            if (typeof this.pricePlan === 'undefined') {
                this.priceDatas = this.priceData;
            } else {
                this.getTableData();
            }
        }
    }
</script>
<style lang="scss">
    @import "../../assets/main.scss";

</style>



