<template>
  <div>
    <!--客户电话号码-->
    <dialogs type="customer" width="36em" ref="editCstPhonesWin" v-if="optType=='add'" overflow="auto">
      <div slot="header">
        {{!!title?title:'管理客户联系电话'}}
      </div>
      <div slot="content">
         <phoneChild :customerInfo="customerInfo" @closeWin="closePhoneWin" @submit="saveCstPhones" :optType="optType" ></phoneChild>
      </div>
     </dialogs>
     <div v-if="optType!=='add'" >
      <phoneChild :customerInfo="customerInfo"  @closeWin="closePage"  @submit="saveCstPhones" :optType="optType" ></phoneChild>
     </div>

    <dialogs type="alert" ref="alerts"></dialogs>
    <dialogs type="confirm" ref="confirms"></dialogs>
  </div>
</template>
<script>
  import phoneChild from '../../files/staff/phoneChild.vue'
  export default {
    data() {
      return {
        cstPhones:{mobilePhones:null}
      }
    },
    components: {
      phoneChild
    },
    props: ['customerInfo', "optType", 'title'],
    methods: {
      //保存电话号码
      saveCstPhones: function (param) {

        let that = this;
          that.cstPhones.mobilePhones=param;
          if(that.cstPhones.mobilePhones.length == 0) {
            that.$refs.alerts.show({
              msg: "请至少添加一个手机号!"
            });
          } else {
            if (this.optType == 'edit') {
              let val="";
              for (let i = 0; i < that.cstPhones.mobilePhones.length; i++) {
                let item = that.cstPhones.mobilePhones[i];
                if (item.optType == 'add') {
                  val = item.phone;
                  break;
                }
              }
              that.$emit('eidtContact',val,function () {
                that.$refs.confirms.show({
                  msg: '确认提交客户电话号码修改，电话号码删除将会自动解绑？',
                  action: {
                    confirm: function () {
                      that.submitCstPhoneSave(that.customerInfo.customerNo, that.cstPhones.mobilePhones);
                    }
                  }
                });
              });
            }
            else {
              this.closePhoneWin(true);
            }
        }
      
      },
     
      submitCstPhoneSave: function (customerNo, mobilePhones) {
        let that = this;
        let isDelete = false;
        let submitParam = {customerNo: customerNo, mobilePhones: []};
        for (let i = 0; i < mobilePhones.length; i++) {
          let item = mobilePhones[i];
          if (item.state == 0 || (item.state == 1 && item.optType == 'delete')) {
            submitParam.mobilePhones.push({phone: item.phone, optType: item.optType});
          }
          if (item.optType == 'delete') {
            isDelete = true;
          }
        }
        that.getRequest({
          url: 'customerInfo/updatePhoneBind',
          param: submitParam,
          method: 'post',
          success: function (data) {
            that.$refs.alerts.show({
              msg: "客户信息提交成功!"
            });
           
            that.closePhoneWin();
          },
          error: function (data) {
            that.closePhoneWin();
            that.$refs.alerts.show({
              msg: data.message
            });
          }
        });
      },
    
      //关闭电话号码弹出框
      closePhoneWin: function (flag) {
        if(this.optType === 'add') {
              this.$refs.editCstPhonesWin.close();
        }
        this.$emit('getUserPhone', flag ? this.cstPhones.mobilePhones : this.customerInfo.mobilePhones);
      },

      closePage:function(){
        this.$emit('closeWin');

      },

      show: function () {
        let that = this;
        that.$nextTick(function () {
          that.$refs.editCstPhonesWin.show();
          setTimeout(function () {
            if(that.$refs.editCstPhonesWin && that.$refs.editCstPhonesWin.$children[0]) {
              that.$refs.editCstPhonesWin.$children[0].$children[0].loadPhones()
            }
          },0)
        });
      },
 
    },
    mounted: function () {

    },
    watch: {
      'customerInfo': {
        handler (val){
      
        }
      }
    }

  }

</script>
<style></style>



