    methods: {
      cstTabClick:function(index){
         this.cstTabIndex=index;
      },
      getSelectPhone: function (item ,index) {
        this.wxBinds[index].isSel =  !this.wxBinds[index].isSel;
      },
      //#region 微信资料窗口
      cstPhoneDel:function(item){
        this.submitDelWx([item.id]);
      },
      //微信解绑
      delWxBind:function(){
        let that = this;
        let idAttr=[];
        for (let i = 0; i < this.wxBinds.length; i++) {
          let item = this.wxBinds[i];
          if (item.isSel) {

            idAttr.push(item.id);
          }
        }
        if (idAttr.length > 0) {
          that.submitDelWx(idAttr);
        }
        else {
          that.alerts.show({
            msg: "请选择需要解绑的手机号码!"
          });
        }
      },
      submitDelWx:function(idAttr){
        let that = this;
        that.$refs.confirms.show({
          msg: '确定解绑微信？',
          action: {
            confirm: function () {
              that.getRequest({
                url: 'appPay/unbindWeChat',
                param: {ids: idAttr.join()},
                method: 'post',
                success: function (data) {
                  if (data.code == 1) {
                    that.seachWxBind();
                  }
                  that.alerts.show({
                    msg: data.message
                  });
                },
                error: function (data) {
                  that.alerts.show({
                    msg: data.message
                  });
                }
              }, true);
            }
          }
        });
      },
      
      seachWxBind:function(){
        let that=this;
        that.getRequest({
          url: '/customerInfo/selWXBind',
          param: {customerNo: this.customerInfo.customerNo,cno:""},
          method: 'post',
          success: function (data) {

            that.wxBinds=[];
            if ( data.data && data.data.length > 0) {
              let Data = data.data;
              let len = Data.length;
              for(let i=0; i<len; i++) {
                 that.wxBinds.push(Data[i]);
                 that.wxBinds[i]['isSel'] = false;

              }

            }
          },
          error: function (data) {
            that.alerts.show({
              msg: data.message
            });
          }
        }, true);
      },

      //#endregion
      getValues: function (val) {
        if (val !== '' && typeof val !== 'undefined' && val !== null) {
          return val.toString().replace(/<\/?.+?>/g, "").replace(/ /g, "")
        }
        return '--'
      },
      btnEvent: function (funName, param) {
        this[funName](param);
      },
      getJZQInfo: function (guid) {
        let that = this;
        that.getWebsocket({
          keyMsg: 'jzqReadCustomerInfo',
          keyStatus: 'jzqReadCustomerInfoStatus',
          paramStatus: {
            guid: guid,
            dataFlag: 'jzqReadCustomerInfoStatus'
          },
          funMsg: function (data) {
            that.resetDialogData(data)
          }
        })
      },
      resetDialogData: function (data) {
        let that = this;
        that.loadings.close();
        that.getItemConfig(data.data, 'value2');
        that.showCompare = true;
        setTimeout(function () {
          that.$refs.showDevieConfig.show({
            hasClose: true,
            action: {
              cancel: function () {
                 that.showCompare = false;
              }
            }
          });
        }, 0)
      },
      getConfig: function (device) {
        let that = this;
        let guid = new GUID().newGUID();
        that.loadings.show({
          msg: '正在读取数据，请稍后。。。',
          hasClose: true,
          action: {
            cancel: function () {
              that.loadings.close();
              that.$store.state.callback['jzqReadCustomerInfoStatus'] = null;
              that.$store.state.callback['jzqReadCustomer'] = null;
              clearInterval(that.$store.state.setRecall);
            }
          }
        });
        that.currentConfigDevice = device;
        that.getItemConfig(device, 'value1');
        that.getRequest({
          url: 'customerInfo/jzqReadCustomerInfo',
          param: {cno: device.cno, jzqNo: device.jzqNo, commSetupSn: device.commSetupSn, guid: guid},
          method: 'post',
          success: function (data) {
            that.getJZQInfo(guid);
          },
          error: function (data) {
            that.loadings.close();
            that.alerts.show({
              msg: data.message
            });
          }
        }, true)
      },
      getItemConfig: function (device, key) {
        let that = this;
        let len = that.deviceConfigRowData.length;
        for (let i = 0; i < len; i++) {
          let item = that.deviceConfigRowData[i];
          let value = device[item.key];

          if (item.key == 'commPort' && key == 'value1') {

            value = this.getEnumItem('commPort', device[item.key]).text;
          } else if (item.key == 'commRule' && key == 'value1') {
            value = value == 30 ? 'DL/T645-2007' : 'DL/T645-1997';
          } else if (item.key == 'ratio' && key == 'value2') {
            value = device['feeRateNum'];
          } else if (item.key == 'ratio' && key == 'value1') {
            value = 4;
          }
          that.$set(that.deviceConfigRowData[i], key, value);
        }

      },
      setImportant: function (device) {
        let that = this;
        that.getRequest({
          url: 'importantCustomer/setImportant',
          method: 'post',
          param: {
            cno: device.cno,
            isImportant: device.isImportant == 1 ? 0 : 1
          },
          success: function (data) {
            that.alerts.show({
              msg: data.message
            });
            that.getStaffInfo();
          },
          error: function (data) {
            that.alerts.show({
              msg: data.message
            });
          }
        }, true)

      },
      getDeviceAccountInfo: function (device) {

        return [
