      //提交修改或者添加设备的数据
      submitData(val, type) {
        let that = this;
        let param = val;
        param.customerNo = that.customerInfo.customerNo;
        param.orgNo = that.customerInfo.orgNo;
        let url = '';
        if (type === 0) {
          param.offScheme = that.customerInfo.offScheme;
          if( param.offScheme == 1) {
               param.offParam = that.customerInfo.offParam;
          }
          switch (val.deviceType) {
            case '07':
              //   param.moteType = 'C';
              //添加电表
              url = 'customerInfo/addSingleElectricDevice';
              break;
            case '08':
              //     param.meterType = 0;
              url = 'customerInfo/addSingleWaterDevice';
              break;
            case '09':
              //  param.meterType = 0;
              url = 'customerInfo/addSingleGasDevice';
              break;
          }
        } else {
          switch (val.deviceType) {
            case '07':
              //修改电表
              url = 'customerInfo/editElectricDevice';
              break;
            case '08':
              url = 'customerInfo/editWaterDevice';
              break;
            case '09':
              url = 'customerInfo/editGasDevice';
              break;
          }
        }
        // console.log(param);
        that.getRequest({
          url: url,
          method: 'post',
          param: param,
          success: function (data) {
            that.alerts.show({
              msg: data.message
            });
            that.getStaffInfo();
          },
          error: function (data) {
            that.alerts.show({
              msg: data.message
            });
          }
        })
