      //换表弹框
      changeDeviceInfo: function (device) {
        this.deviceInfo = device;
        let that = this;
        that.showOperates = true;
        that.getRequest({
           url: 'changeMeter/queryOldReadValue',
           method: 'post',
           param: {customerNo: that.customerInfo.customerNo, oldCno: that.deviceInfo.cno},
           success: function (data){
              let Data = data.data;
              for(let key in Data) {
                that.oldDevice[key] = Data[key]
              }
              setTimeout(function () {
                   that.$refs.changeDevice.show();
               }, 0)
           }
        }, true);
      },
      //提交换表信息
      submitChange: function () {
        //'oldDevice.remainAmount', 'newDevice.remainAmount',
        let request = [];
        this.changeDevice['customerNo'] = this.customerInfo.customerNo;
        let url = '';
        switch (this.deviceType) {
          case '07':
            this.newDevice.localControl = this.deviceInfo.localControl;
         
            request = ['newDevice.deviceNo', 'newDevice.readValue', 'oldDevice.readValue','changeDevice.changeRemark'] ;
            url = 'changeMeter/changeElectricMeter';
            if(parseInt(this.newDevice.commFactorCnt) > 1) {
               request.push('newDevice.readValue1');
            
               request.push('newDevice.readValue2');
              
               request.push('newDevice.readValue3');
               
               request.push('newDevice.readValue4');
               
            }
            if(parseInt(this.oldDevice.commFactorCnt) > 1) {
         
               request.push('oldDevice.readValue1');
            
               request.push('oldDevice.readValue2');
            
               request.push('oldDevice.readValue3');
             
               request.push('oldDevice.readValue4');
            }
            break;
          case '08':
            url = 'changeMeter/changeWaterMeter';
            request = ['newDevice.deviceNo', 'newDevice.readValue', 'oldDevice.readValue',  'changeDevice.changeRemark'];
            break;
          case '09':
            url = 'changeMeter/changeGasMeter';
            request = ['newDevice.deviceNo', 'newDevice.readValue', 'oldDevice.readValue',  'changeDevice.changeRemark'];
            break;
        }
        this.newDevice.remainAmount = this.oldDevice.remainAmount = '0';
        let that = this;
        that.regularTest({
          emptyKey: request,
          callback: function () {
            that.newDevice = that.getSearchData(that.newDevice);
            that.oldDevice = that.getSearchData(that.oldDevice);
            switch (that.deviceType) {
              case '07':
                that.changeDevice['newElectricMeter'] = that.newDevice;
                that.changeDevice['oldElectricMeter'] = that.oldDevice;
                break;
              case '08':
                that.changeDevice['newWaterMeter'] = that.newDevice;
                that.changeDevice['oldWaterMeter'] = that.oldDevice;
                break;
              case '09':
                that.changeDevice['newGasMeter'] = that.newDevice;
                that.changeDevice['oldGasMeter'] = that.oldDevice;
                break;
            }
            if (that.newDevice.deviceNo === that.deviceInfo.deviceNo) {
              that.alerts.show({
                msg: '旧表号与新表号相同，不能进行换表操作！'
              });
            } else {
              if(typeof that.newDevice.paramFlag == 'undefined' || that.newDevice.paramFlag === '') {
                    that.$refs.alerts.show({
                        msg: '设备型号不能为空！'
                    });
              } else {
                 if(that.judegeReadValue(that.newDevice, url, false) && that.judegeReadValue(that.oldDevice, url, true)) {
                    that.changeSubmit(url);
                 } 
                }
              
            }
          }
        });
      },
      changeSubmit: function (url) {
         let that = this;
              that.loadings.show({
                msg: '正在进行换表，请稍后。。。'
              });
              that.getRequest({
                url: url,
                param: that.changeDevice,
                method: 'post',
                success: function (data) {
                  that.loadings.close();
                  that.alerts.show({
                    msg: data.message
                  });
                  that.changeDevice = { //换表
                    changeRemark: ''
                  };
                  that.getStaffInfo();
                  that.closeDialog();
                },
                error: function (data) {
                  that.loadings.close();
                  that.alerts.show({
                    msg: data.message
                  })
                }
              })
      },
      juegeEveryData: function (obj, key1,key2, text) {
       
        if(parseFloat(this.oldDevice[key1].toString()) > parseFloat(obj[key2].toString())){
            this.alerts.show({msg: `${text}的值应大于上一次抄表示数${this.oldDevice[key1].toString()}`});
            return false;
        } 
        return true
      },
      judegeReadValue: function (obj, url, f) {
        let flag = true;
        if(f) {
           if(parseInt(obj.commFactorCnt) == 1 ) {
              flag = this.juegeEveryData(obj,'lastReadValue', 'readValue', '总示数');
           } else if(parseInt(obj.commFactorCnt) > 1 && this.deviceType == '07'){
              flag = this.juegeEveryData(obj,'lastReadValue', 'readValue', '总示数')
               &&this.juegeEveryData(obj,'lastReadValue1', 'readValue1', '尖示数')
               &&this.juegeEveryData(obj,'lastReadValue2', 'readValue2', '峰示数')
               &&this.juegeEveryData(obj,'lastReadValue3', 'readValue3', '平示数')
               &&this.juegeEveryData(obj,'lastReadValue4', 'readValue4', '谷示数');
           }
        }   
       if(flag) {
         if(parseInt(obj.commFactorCnt) > 1 && this.deviceType == '07') {
          let that = this;
          let total = parseFloat(obj.readValue.toString());
          let top1 = parseFloat(obj.readValue1.toString());
          let top2 = parseFloat(obj.readValue2.toString());
          let top3 = parseFloat(obj.readValue3.toString());
          let top4 = parseFloat(obj.readValue4.toString());
          let c = Math.abs(total - (top1 + top2 +top3 +top4)).toFixed(4);
       
          if(c == 0) {
               return true;
          } else if (c <= 0.02 && c > 0) {
             that.$refs.confirms.show({
                 msg: '尖峰平谷之和与总示数之间存在 ' + c + ' 的偏差，确定保存吗？',
                 action: {
                    confirm: function () {
                      that.changeSubmit(url);
                      return true;
                    }
                  }
              })
          } else {
               that.alerts.show({msg: '尖峰平谷之和与总示数之间偏差不能大于0.02！'});
               return false
          }
         } else {
           return true;
         }
       } else {
        return false;
       }  
      },
