      //获取当前设备信息
      getDeviceInfo: function (data) {
        let device = this.deviceRecords[this.deviceType];
        if (device === '') {
          this.noData = true;
          this.noCardShow = true;
        } else {
          this.noData = false;
          if (device.isAccount !== 0) {
            if (device.accountInfo === null) {
              this.noCardShow = true;
            } else {
              this.noCardShow = false;
              this.cardInfo = device.accountInfo;
            }
          } else if (data && device.isAccount === 0) {
            this.noCardShow = false;
            this.cardInfo = device.accountInfo = this.deviceRecords[this.deviceType].accountInfo = data;
          } else {
            this.noCardShow = true;
            this.cardInfo = this.cleanSearchData(this.cardInfo);
          }
          this.deviceInfo = device;
        }

      },
      //获取个人和所有设备资料
      getStaffInfo: function (val) {
  
        let that = this;
        this.getRequest({
          url: 'customerInfo/queryDetails',
          method: 'post',
//                    method: 'get',
//                    url: 'dist/json/staffDetail.json',
          param: {customerNo: that.selected.customerNo},
          success: function (data) {
            let allData = data.data;
            let info = allData.customerInfo;
            that.customerInfo =  {};
            for(let key in info) {
              that.$set(that.customerInfo, key, info[key])
            }
          
            that.customerInfo['createTime'] = that.customerInfo['createTime'] ? that.customerInfo['createTime'].substr(0, 19) : '--';
            allData.electricMeter ? (that.deviceRecords['07'] = allData.electricMeter, that.tabs[0].num = allData.electricMeter.length) : (that.deviceRecords['07'] = [], that.tabs[0].num = 0);
            allData.waterMeter ? (that.deviceRecords['08'] = allData.waterMeter, that.tabs[1].num = allData.waterMeter.length) : (that.deviceRecords['08'] = [], that.tabs[1].num = 0);
            allData.gasMeter ? (that.deviceRecords['09'] = allData.gasMeter, that.tabs[2].num = allData.gasMeter.length) : (that.deviceRecords['09'] = [], that.tabs[2].num = 0);
            that.getTab(that.$store.state.customerDeviceIndex);
           // if(that.customerInfo.isEnabled != 0 && that.controls[2]){
                that.seachWxBind();
           // }
          },
          error: function (data) {
            that.alerts.show({
              msg: data.message
            })
          }
        }, true)
      },
      init: function () {
        this.selected = this.row;
        this.getStaffInfo();
      }
    },
    mounted: function () {
      this.isIn();
      this.init();
      this.$store.state.customerDeviceIndex = 0;
      //   this.getAction();
    },
    watch: {}
  }
</script>
<style lang="scss">
  @import "../../../assets/main.scss";
  //没有数据的div显示
  .emptyDivShow {
    text-align: left;
    padding-left: 0.3em;
    font-size: 0.83em
  }

  .operate-container {
    width: 6em !important;
    margin: 0px !important;
    position: absolute;
    height: 100%;
    right: 0;
    .device-div {
      position: absolute;
      right: 0;
      top: calc(50% - 5em);
      width: 100%;
      text-align: center;
      button {
        font-size: 0.84em;
      }
    }
  }

  .detailInfo-div {
    margin-top:0.7em;
    height: calc(100% - 5.7em);
    padding:0 0.5em;
    position: relative;
    float: left;
    margin-bottom: 0.5em;
    width: calc(100% - 1.2em);
    .angle-device {
      margin-bottom: 0em;
      padding-bottom: 1em;
      width:100%;
      position: relative;
      .list-header{
        margin-bottom:0;
        padding: 1em  0;
        border:0
      }
    }
    .angle-device:last-child {
      border-bottom: 0 !important;
    }
    .angle-device:after {
      @extend .clearElement
    }
  }

  .noBorderTab {
    li {
      margin-right: 0;
      img {
        float: none !important;
      }
      img + span {
        //margin: 0.1em 0 0 0.4em!important;
      }
    }
  }

  .changeTable {
   
    .special-table {
      border: 0 !important;
      font-size: 1em;
      td {
        padding: 0.4em;
        background: transparent;
        border-bottom: 0;
        border-color: #4D5F76;
        text-align: left;
        padding-left: 0.2%;
       .selectBtn span{
           top: 11px
         }
       
      }
      td:last-child {
        border: 0;
      }
      .table-header {
        border-bottom: 1px solid #4D5F76;
        background: transparent;
        th {
          border: 0 !important;
          font-weight: 100;
        }
      }
    }

  }

  .hasAction {
    text-decoration: underLine;
    color: #69A4E7;
    cursor: pointer;
  }

  .simpleCstTab{
    font-size: 1.05em;
    li{
      margin: 0 0.2em;
      float: left;
      cursor: pointer;
      color: white;
      //background: rgba(#3588fa,0.3);
      background: #15254B; 
      span{
        color:inherit;
        display: block;
        height:100%;
        padding: 0.7em 2.3em 0.5em 2.3em;
        border-bottom: 0.2em solid transparent;
      }
    }
    li:first-child{
      margin-left:0
    }
    li span:hover,.selTab{
      border-bottom: 0.2em solid #227fdf;
      background: rgba(255,255,255, 0.1);
    }
    .router-link-exact-active,li a.router-link-exact-active:hover{
      background: #227fdf;
      border-color:transparent;
    }

  }
  .simpleCstTab:after{
    clear:both;
    content:'';
    display:block;
    width:1px;
    height:0;
  }
  .tabCstContent{
    width:100%;
    height: calc(100% - 4em);
  
  }
</style>



