<template>
  <div ref="trys" style="height: calc(100%)">
    <div class="all-container noBackAll" >
       <div class="con-left leftTree leftTrees">
          <ul style="font-size:0.88em;">
            <template v-for="(model, $index) in treeData" v-if="treeData.length > 0"> 
                <items @selClicks="selNodeClick" class="item"  :model="model" textKey="nodeName" :valueKey = "treeKey" :onBrother="true" :isInit="true"></items>
            </template>
          </ul>
         <!--  <button @click="getTreeData()">refresh</button> -->
       </div>
       <div class="con_center con_centers"  v-show="!showAdd && !showInfo">
         <div class="operate-div" ref="operateDiv">
          <template v-for="(item, $index) in FormOpt">
            <inputs ref="inputs" v-if="item.type == 'input'" :option="item.options"
            @getInput="getFrom"></inputs>
          </template>
         </div>

         <div class="operate-div operateBtn">
          <!--v-if="controls[1]" v-if="controls[3]" v-if="controls[8]" v-if="controls[9]"-->
            <button class="button-operate"  v-if="searchParam.nodeName!== ''" @click="removeTreeSelect">
               已选择{{searchParam.nodeName.replace(/\(.*?\)/g,'')}}
              <i class="hasIcon hasImg" style="float:right;margin-right:0;margin-left:0.6em">
                <img src="dist/img/face/close-icon.png"  />
              </i>
            </button>
            <button class="button-operate" @click="addUser"  v-if="controls[1]">
              <i class="hasIcon hasImg">
                <img src="dist/img/file/add-user-button.png"  />
              </i>添加
            </button>
            <button class="button-operate" @click="removeUser"  v-if="controls[3]">
              <i class="hasIcon hasImg">
                <img src="dist/img/file/delete-user-button.png" />
              </i>删除
            </button>
            <button class="button-operate" @click="issuedUser" v-if="controls[8]">
              <i class="hasIcon hasImg">
                <img src="dist/img/file/load-file-button.png" />
              </i>下发
            </button>
            <button class="button-operate" @click="importUser" v-if="controls[9]">
              <i class="hasIcon hasImg">
                <img src="dist/img/file/reload-file-buttton.png" />
              </i>导入
            </button>
         </div>
         <div ref="myOwnTable" class="table-container" :style="{height: tableHeight || 'calc(100% - 4.5em)'}">                                                                
              <tables ref="myTable" v-if="showTable" :tableOpt="TableOpt" class="tablePadding" @rowDbClick="clickTableRow"></tables>
          </div>
        </div>
       <div class="con_center con_centers" v-if="showInfo" >
          <staffInfo :row="row" @closeDiv="closeDiv" :controls="controls" ref="infoDetail"></staffInfo>
       </div>
        <div class="con_center con_centers" v-if="showAdd" >
           <staffAdd :row="row" @closeDiv="closeDiv" :controls="controls" 
               :buildNo="searchParam.nodeType == 2 ? searchParam.nodeNo : '' " 
               :departNo="searchParam.nodeType == 1 ? searchParam.nodeNo : searchParam.pNodeNo"
               >
           </staffAdd>
        </div>
    </div>
    <dialogs type="customer" width="30em" ref="resolute" v-if="showDialog">
      <div slot="header">
        导入结果
      </div>
      <div slot="content">
        <p style="font-size: 16px;text-align: center;line-height: 40px "> 客户资料成功导入<b>
          {{importInfo.successCount}} </b>户， 失败 <b>{{importInfo.failCount}} </b>户；</p>
      </div>
      <div slot="footer">
        <button  v-if="importInfo.failCount>0" @click="importFailExcel"
                 class="button-default"
                 data-dismiss="modal">下载失败资料
        </button>
        <button class="button-default" @click="closeDialog">关闭</button>
      </div>
    </dialogs>
    <dialogs type="alert" ref="alerts"></dialogs>
    <dialogs type="loading" ref="loadings"></dialogs>
    <dialogs type="loading" ref="progress"></dialogs>
    <dialogs type="confirm" ref="conformDialog"></dialogs>
    <fileInput ref="uploadTarget" :upLoadAttrParam="upLoadFile.upLoadAttrite"
               :ForthwithLoadParam="upLoadFile.ForthwithLoad" @upLoadFileFunc="analyzeExcel"></fileInput>
  </div>
</template>
<script>
    import {GUID} from '../../../assets/GUID.js'
    import fileInput from '../../childComponent/UploadFile.vue'
    import staffInfo from './Info.vue'
    import staffAdd from './Add.vue'
    import items from '../../childComponent/treeItem.vue'
    export default {
        data () {
            return {
                treeKey: ['nodeNo','pNodeNo'],
                treeData: [],
                tableHeight: '',
                interVal: '', //下发档案中的轮循
                Guid: '', //下发档案GUID
                row: '', //选中table行数据，将传递给详情页面
                showAdd: false, //显示添加页面
                showInfo: false,//显示详情页面
                showTable: false, //table延迟加载
                importInfo: { //导入档案
                  importId: '',
                  successCount: 20,
                  failCount: 20,
                },
                searchParam: {
                  customerName: '',
                  propertyName: '',
                  customerContact: '',
                  customerAddr: '',
                  customerId: '',
                  deviceNo: '',
                  nodeNo: '',
                  pNodeNo: '',
                  nodeType: '',
                  nodeName: ''
                }, //表格的查询操作
                controls: [],
                signMsg: 'start', //轮循标志
                searchType: '',
                selected: '', //选中table行数据，数组类型，用于删除和下发的批量操作
                FormOpt: [
                    {
                        type: 'input',
                        options: {
                            type: 'typeSearch',
                            key: 'searchParam',
                            value: '',
                            selectOpt: {
                                width: '7em',
                                cs: 'client',
                                data: [
                                    {text: '用户姓名', value: 'customerName'},
                                    {text: '门牌编号', value: 'propertyName'},
                                    {text: '联系电话', value: 'customerContact'},
                                    {text: '用户地址', value: 'customerAddr'},
                                    {text: '表计户号', value: 'meterUserNo'},
                                    {text: '用户表号', value: 'deviceNo'}
                                ],
                                text: 'text',
                                value: 'value',
                                key: 'searchType',
                                constant: {value: 'customerName'}
                            }
                        }
                    }
                ],
                TableOpt: {
                    method: 'post',
                    url: 'customerInfo/queryListNew',
                    column: [
                        {field: 'state', type: 'checkBox', textAlign: 'center', width: '60'},
                        {
                            title: '用户状态',
                            field: 'isEnabled',
                            textAlign: 'center',
                            width: '100',
                            sortable: false,
                            type: 'string',
                            formatter: function (val, index, row) {
                                if (val === 1) {
                                    return '<span class="green">正常</span>';
                                }
                                else {
                                    return '<span class="red">销户</span>'
                                }
                            }
                        },
                      {
                        title: '下发状态',
                        field: 'sendFlag',
                        textAlign: 'center',
                        width: '100',
                        sortable: false,
                        type: 'string',
                        formatter: function (val, index, row) {
                          if (val === 1) {
                            return '<span style="color: #0A9DE2">已下发</span>';
                          }
                          else {
                            return '<span style="color: #F34B4A">未下发</span>'
                          }
                        }
                      },
                        {title: '用户姓名', field: 'customerName', textAlign: 'center', width: '160', sortable: false},
                        {title: '门牌编号', field: 'propertyName', textAlign: 'center', width: '180', sortable: false},
                        {title: '电表', field: 'electricDeviceNo', textAlign: 'center', width: '180',  sortable: false,type: 'string', 
                          formatter:function (val,index,row) {
                            if(row.deviceNoParam) {
                               return row.deviceNoParam.electricDeviceNo || ''
                            }
                          }
                        },
                         {title: '水表', field: 'waterDeviceNo', textAlign: 'center', width: '180',  sortable: false,type: 'string', 
                           formatter:function (val,index,row) {
                            if(row.deviceNoParam) {
                               return row.deviceNoParam.waterDeviceNo || ''
                            }
                          }
                        },
                        {title: '气表', field: 'gasDeviceNo', textAlign: 'center', width: '180',  sortable: false,type: 'string', 
                          formatter:function (val,index,row) {
                           if(row.deviceNoParam) {
                               return row.deviceNoParam.gasDeviceNo || ''
                            }
                          }
                        },
                        {title: '联系电话', field: 'customerContact', textAlign: 'center', width: '140', sortable: false},
                        {title: '用户地址', field: 'customerAddr', textAlign: 'center', width: '200', sortable: false},
                        {title: '商家品牌', field: 'customerBrand', textAlign: 'center', width: '200', sortable: false},
                        {title: '部门机构', field: 'orgName', textAlign: 'center', width: '150'},
                        {
                            title: '创建时间',
                            field: 'createTime',
                            textAlign: 'center',
                            width: '160',
                            sortable: false,
                            type: 'string',
                            formatter: function (val) {
                                return val.substr(0, 19)
                            }
                        },
                        {title: '创建人', field: 'createUserName', textAlign: 'center', width: '100'}
                    ],
                    queryParam: {isSearchChild: 1},
                    pageSize: 20,
                    pageNumber: 1,
                    pageList: [20, 40, 60],
                    sortName: 'createTime',
                    sortOrder: 'desc',
                    rowName: 'data',
                    isExport: true,
                    exportURL: 'customerInfo/downloadCustomerInfo',
                    noExportCurrent: true
                },
                deepSearchData: [],
                upLoadFile: {
                    //即时上传
                    ForthwithLoad: true,
                    //上传属性
                    upLoadAttrite: {
                        url: 'customerInfo/uploadFile',
                        extensions: ['xls', 'xlsx'], //上传文件的后缀
                        fileSize: 30000,//文件的大小10M
                        templateUrl: 'dist/modal/用户档案模板.xls' //模板路径
                    }
                },
              styleParams: '',
              showDialog: false
            }
        },
        computed: {
            alerts: function () {
                return this.$refs.alerts
            },
            loadings: function () {
                return this.$refs.loadings
            },
            progress: function () {
                return this.$refs.progress
            }
        },
        components: {
            fileInput,
            staffInfo,
            staffAdd,
            items
        },
        methods: {
            removeTreeSelect: function () {
               let current = {};
               for(let item in this.treeData[0]) {
                  if(item !== 'children') {
                   current[item] = this.treeData[0][item];
                }
              }
              this.$store.state.currentSelectOrg = current;
              this.searchParam.nodeNo = current.nodeNo;
              this.searchParam.nodeType = current.nodeType;
              this.searchParam.pNodeNo = current.pNodeNo;
              this.searchParam.nodeName = current.nodeName;
              this.getParams(1);
            },
            selNodeClick: function (node) {
              this.searchParam.nodeType = node.nodeType;
              this.searchParam.nodeNo = node.nodeNo;
              this.searchParam.nodeName = node.nodeName;
              switch(node.nodeType) {
                case 1: {
                  this.searchParam.pNodeNo = '';
                  break
                };
                case 2: {
                  this.searchParam.pNodeNo = node.pNodeNo;
                  break
                }
              }
              this.getParams(1);
            },
            getTreeData: function (){
              let that = this;
              that.getRequest({
                url: 'tree/buildingTree',
               // url: 'dist/json/departments.json',
                method: 'get',
                success: function (data) {
                   that.treeData = data.data;
                   if(!that.showTable) {
                       that.TableOpt.isExport = that.controls[6];
                       let item = data.data[0];
                       that.TableOpt.queryParam.nodeNo = that.searchParam.nodeNo = item.nodeNo;
                       that.TableOpt.queryParam.nodeType = that.searchParam.nodeType = item.nodeType;
                       that.TableOpt.queryParam.pNodeNo = that.searchParam.pNodeNo = item.pNodeNo;
                       that.TableOpt.queryParam.nodeName = that.searchParam.nodeName = item.nodeName;
                       that.showTable = true;
                   }
                }
              })
            },
            getFrom: function (val) {
              if (val.combine) {
                if (this.searchParam[val.key.split('.')[1]] !== val.val) {
                  this.searchParam = this.cleanSearchData(this.searchParam,  ['nodeType', 'nodeNo', 'pNodeNo', 'nodeName']);
                  this.getOption(val, true);
                  this.getParams(1);
                }
              } else {
                this.getOption(val, true);
              }
            },
            removeSearchData: function (index) {
                let key = this.deepSearchData[index].key;
                this.searchParam[key] = this.$refs['inputs'][0].search = '';
                this.getParams(1);
            },
            closeDiv: function (val) {
                this.showAdd = false;
                this.showInfo = false;
                if (val) {
                    this.searchParam = this.cleanSearchData(this.searchParam, ['nodeType', 'nodeNo', 'pNodeNo', 'nodeName']);
                    this.$refs.inputs[0].search =  '';
                    this.getParams(1);
                    this.getTreeData()
                } else {
                    this.getParams();
                }
            },
            closeDialog: function () {
                this.showDialog = false;
                this.$refs.resolute.close();
            },
            //显示上传结构弹出框
            centerDialog: function () {
                let that = this;
                that.showDialog = true;
                setTimeout(function () {
                   that.$refs.resolute.show();
                }, 0)
            },
            analyzeExcel: function () {
                let that = this;
                let reqpara = {
                    saveName: that.$refs.uploadTarget.upLoadedData.saveName,
                    fileName: that.$refs.uploadTarget.upLoadedData.fileName
                };
                that.loadings.show({
                    msg: '正在解析excel文件,请稍后...'
                });
                //提交解析数据
                that.getRequest({
                    url: 'customerInfo/batchSave',
                    method: 'post',
                    param: reqpara,
                    success: function (data) {
                        that.loadings.close();
                        let Data = data.data;
                        that.importInfo.importId = that.$refs.uploadTarget.upLoadedData.importId;
                        that.importInfo.successCount = Data.successCount;
                        that.importInfo.failCount = Data.failCount;
                        that.centerDialog();
                        that.getTreeData();
                        that.$nextTick(function () {
                           that.removeTreeSelect();
                        })
                    },
                  error: function (data) {
                    that.loadings.close();
                     that.alerts.show({
                      msg: data.message
                    });
                  }
                }, true);
            },
            importFailExcel: function () {
                if (this.importInfo.failCount > 0) {
                    let reqpara = {
                        importId: this.importInfo.importId,
                        isDown: 1
                    };
                    this.downLoad({url: 'customerInfo/downloadErrorData', data: reqpara})

                }
            },
            addUser: function () {
                this.showAdd = true
            },
            //下发档案
            issuedUser: function () {
                let that = this;
                this.getConfirm({
                    emptyMsg: '您还未选择任何用户，不能进行下发!',
                    confirmMsg: '您确定要下发选中用户的档案吗？',
                    callback: {
                      confirm: function () {
                        let params = [];
                    //    let arr = ['electricDeviceCno', 'waterDeviceCno', 'gasDeviceCno'];
                        for (let i = 0; i < that.selected.length; i++) {
                          let current = that.selected[i];
                          let arr = current.cnoList;
                          for (let j = 0; j< arr.length; j++) {
                              let dev = arr[j];
                              if(typeof dev !== 'undefined' && dev !== null && dev !== '' && dev !== 'undefined') {
                                     params.push(dev)
                              }

                          }  
                        }
                        that.Guid = new GUID().newGUID();
                        that.signMsg = 'start';
                        that.getData(that.Guid, params);
                      }
                    }
                  })
            },
          //下发档案（轮询获取指令执行情况）
            reCall: function (p, flag) {
                let that = this;
                that.interVal = setInterval(function () {
                    if (that.signMsg === 'start') {
                        that.signMsg = '';
                        that.getRequest({
                            url: 'customerInfo/readSyncState',
                            method: 'post',
                            param: {guid: p.guid},
                            success: function (data) {
                                let code = data.data;
                                if (code === 101) {
                                    that.signMsg = 'start';
                                } else {
                                    if(flag) {
                                       that.$refs.infoDetail.getStaffInfo()
                                    } else {
                                       that.getParams();
                                    }
                                    clearInterval(that.interVal);
                                    that.selected = '';
                                    that.progress.close();
                                    that.signMsg = 'start';
                                    that.alerts.show({
                                          msg: data.message
                                    })
                                    
                                }
                            },
                          error: function (data) {
                            that.getParams();
                            clearInterval(that.interVal);
                            that.selected = 'start';
                            that.progress.close();
                            that.alerts.show({
                              msg: data.message
                            })
                          }
                        }, true);
                    }
                }, 1000)
            },
           //下发档案（下发指令）
            getData: function (guid, params, flag) {
                let that = this;
                that.progress.show({
                    msg: '正在下发数据，请稍后......',
                    hasClose: true,
                    action: {
                      cancel: function () {
                        that.getParams(1);
                        that.progress.close();
                        that.getRequest({
                          url: 'customerInfo/cancelSyncMeterInfo',
                          method: 'post',
                          param: {guid: guid}
                        }, true);
                        that.signMsg = '';
                        clearInterval(that.interVal);
                      }
                    }
                });
                let p = {cnos: params, guid: guid};
                that.getRequest({
                    url: 'customerInfo/syncMeterInfo',
                    method: 'post',
                    param: p,
                    success: function () {
                       let f = flag ?  true : false;
                       that.reCall(p,f)
                    },
                  error: function (data) {
                    that.progress.close();
                    that.getParams();
                    that.alerts.show({
                      msg: data.message
                    })
                  }
                });
            },
            //导入档案
            importUser: function () {
                this.$refs.uploadTarget.show();
            },
            //删除档案
            removeUser: function () {
                let that = this;
                this.getConfirm(
                  {
                    emptyMsg:  '请先选择要删除的用户!',
                    confirmMsg:  '确定要对选中的用户档案进行删除操作吗？',
                    callback: {
                      confirm: function () {
                        that.deleteData({
                          url: 'customerInfo/batchDelete',
                          params: ['customerNo'],
                          callback: function () {
                            that.searchParam = that.cleanSearchData(that.searchParam, ['nodeType', 'nodeNo', 'pNodeNo', 'nodeName']);
                            that.$refs['inputs'][0].search = '';
                            that.getParams(1);
                            that.getTreeData()
                          }
                        })
                      }
                    }
                });
            },
            getParams: function (pageNumber) {
                let arr = this.getSearchData(this.searchParam);
                arr.isSearchChild = 1;
                //console.log(arr)
                this.getHeight();
                this.searchTable(arr, pageNumber);
            },
            clickTableRow: function (val) {
                this.row = val.val;
                this.showInfo = true;
            },
            init: function () {
                let that = this; 
            },
          windowChange: function () {
            let that = this;
            that.getHeight();
            // window.onresize = function () {
            //   that.getHeight();
            // }
          },
        },
        created: function () {
            this.getTreeData();
        },
        mounted: function () {
            let that = this;
            that.isIn();
            that.getAction();
            // window.onresize = function () {
            //   that.windowChange()
            // }
        },
        watch: {
        }
    }
</script>

<style lang="scss">

    button.buttonComL {
        margin: 0;
        border-top-right-radius: 0;
        border-bottom-right-radius: 0;
    }

    button.buttonComR {
        margin: 0 0 0 -3px;
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
    }

    button.buttonComS {
        color: white;
        background: #0A9DE2;
    }

    .formBackIndex {
        z-index: 19
    }

    .formIndex {
        z-index: 20
    }
    .leftTrees{
     // background: rgba(#3588fa, 0.2);
      width: 20em;
      border-radius:0.5em;
      padding: 1em!important;
   
    }
    .con_centers{
       width:calc(100% - 20em - 4em - 0.5em);
      // background: rgba(#3588fa, 0.2);
       border-radius:0.5em;
       height:100%;
       padding: 0em 1em!important;
       .info-div{
        margin-top:2em;
        height: calc(100% - 2em)!important;
        .detail-content{
            padding:0.5em !important
        }
        .info-container{
            height: calc(100% - 0em);
             margin-top: 0em;
        }
       }
    }
   .right-container .center-container .noBackAll{
      padding:0;
      width:100%;
      background: transparent
    } 
</style>



