                   {
                     width: this.sign ? w : widths,
                     type:  this.sign ?   'normalInput' : 'disabledInput',
                     value: this.currentDevice.deviceNo || '',
                     key: 'currentDevice.deviceNo',
                     regular: 'deviceNo',
                     placeHolder: '1-14位数字',
                     length: 14,
                     errorMsg: '水表表号：1-14位数字',
                     limit: 'number'
                   }
                 ]
               },
               {
                 classes:  'halfWidth',
                 label:  '气表表号',
                 show: !this.sign,
                 isEmpty: false,
                 type: 'input',
                 option: {
                   width: widths,
                   type:  'disabledInput',
                   value: this.currentDevice.deviceNo || '',
                   key: 'currentDevice.deviceNo',
                   regular: 'deviceNo',
                   placeHolder: '1-16位数字',
                   length: 16,
                   errorMsg: '气表表号：1-16位数字',
                   limit: 'number'
                 }
               },
              {
                 classes: !this.sign ?  'halfWidth hasMarginLi' : 'halfWidth',
                 type: 'input',
                 isEmpty: false,
                 show: true,
                 label: '表计户号',
                 option: {

                   type: this.sign ?  'normalInput' : 'disabledInput',
                   value: this.currentDevice.meterUserNo || '',
                   length: 12,
                   placeHolder: '1-12位数字',
                   width: widths,
                   limit: 'number',
                   errorMsg: '1-12位数字',
                   key: 'currentDevice.meterUserNo'
                 }
               },
               {
                 classes: !this.sign ?  'halfWidth' : 'halfWidth hasMarginLi',
                 type: 'select',
                 isEmpty: true,
                 show: true,
                 label: '生产厂家',
                 option: {
                   width: widths,
                   cs: 'server',
                   method: 'post',
                   url: 'dictItem/queryByDictCode',
                   param: {dictCode: '14'},
                   rowName: 'data',
                   text: 'dictItemName',
                   value: 'dictItemValue',
                   constant: this.currentDevice.deviceFactoryValue? {dictItemValue:this.currentDevice.deviceFactoryValue}: '' ,
                   key: 'currentDevice.deviceFactoryValue'
                 }
               },
               {
                classes: !this.sign ?  'halfWidth hasMarginLi' : 'halfWidth ',
                 type: 'select',
                 show: typeof this.currentDevice.deviceFactoryValue !== 'undefined' && this.currentDevice.deviceFactoryValue !== '',
                 isEmpty: true,
                 label: '设备型号',
                 option: {
                   width: widths,
                   cs: 'server',
                   method: 'post',
                   url: 'meterConfig/queryByFactory' ,
                   param: {meterFactory: this.currentDevice.deviceFactoryValue, deviceType: this.deviceType},
                   rowName: 'data',
                   text: 'meterMode',
                   value: 'paramFlag',
                   constant: this.currentDevice.paramFlag ? {paramFlag:this.currentDevice.paramFlag }: '' ,
                   key: 'currentDevice.paramFlag'
                 }
               },
               {
                 classes: !this.sign ?  'halfWidth' : 'halfWidth hasMarginLi',
                 type: 'select',
                 isEmpty: false,
                 show: true,
                 label: '采集方式',
                 option: {
                   cs: 'client',
                   width: widths,
                   data: this.getEnumItem('commPorts').obj,
                   constant: this.currentDevice.commPort ? {val:this.currentDevice.commPort} : {val: 32},
                   value: 'val',
                   key: 'currentDevice.commPort'
                 }
               },
              
               // {
               //   classes: !this.sign ?  'halfWidth hasMarginLi' : 'halfWidth ',
               //   type: 'input',
               //   isEmpty: false,
               //   show: true,
               //   label: '预留金额',
               //   option: {
               //     type: this.sign ?  'normalInput' : 'disabledInput',
               //     width: widths,
               //     value: this.currentDevice.initAmount || 0,
               //     key: 'currentDevice.initAmount',
               //     dataType: '+float',
               //     placeHolder: '0-1000之间任意数字,允许两位小数',
               //     length: 7,
               //     regular: 'recharge',
               //     errorMsg: '预留金额：0-1000之间任意数字,允许两位小数',
               //     limit: 'float'
               //   }
               // },
                {
                 classes: !this.sign ? 'halfWidth hasMarginLi' : 'halfWidth ' ,
                 type: 'select',
                 show: true,
                 isEmpty: false,
                 label: '计费方式',
                 option: {
                   width: widths,
                   cs: 'server',
                   method: 'post',
                   url: 'dictItem/queryByDictCode',
                   param: {dictCode: '11'},
                   rowName: 'data',
                   text: 'dictItemName',
                   value: 'dictItemValue',
                   constant: this.currentDevice.dictItemValue ? {dictItemValue:this.currentDevice.dictItemValue }: '' ,
                   key: 'currentDevice.dictItemValue'
                 }
               },
               {
                 classes: !this.sign ? 'halfWidth ' : 'halfWidth hasMarginLi' ,
                 type: 'input',
                 isEmpty: true,
                 label: '楼栋编号',
                 show: true,
                 option: {
                   width: widths,
                   type: 'normalInput',
                   value: this.currentDevice.buildingNo || '',
                   length: 50,
                   placeHolder: '50个字符内',
                   key: 'currentDevice.buildingNo'
                 }
               },
               {
                classes: !this.sign ? 'halfWidth hasMarginLi' : 'halfWidth ' ,
                 type: 'select',
                 show: true,
                 isEmpty: true,
                 label: '重点用户',
                 option: {
                   width: widths,
                   cs: 'client',
                   data: [{text: '否', value: 0}, {text: '是', value: 1}],
                   constant: this.currentDevice.isImportant ? {value:this.currentDevice.isImportant }: '' ,
                   key: 'currentDevice.isImportant'
                 }
               },

               {
                  classes: !this.sign ? 'halfWidth ' : 'halfWidth hasMarginLi' ,
                  type: 'input',
                  show: true,
                  isEmpty: true,
                  label: '变压器号',
                  option: {
                    type:  'normalInput',
                    width: widths,
                    placeHolder: '20个字符内',
                    key: 'currentDevice.transformerNo',
                    value:  this.currentDevice.transformerNo || '' ,
                    length: 20
                 }
               },
               {
                 classes:  'allWidth',
                 type: 'input',
                 show: !(this.currentDevice.commPort == 32),
                 isEmpty: this.currentDevice.commPort == 32,
                 label: '集中器号',
                 option: {
                   type: this.currentDevice.commPort == 32 ? this.jzqType : 'normalInput',
                   width:  widths,
                   key: 'currentDevice.jzqNo',
                   value: this.currentDevice.commPort == 32  ? '999999999' : (this.currentDevice.jzqNo === '999999999' ? '' : this.currentDevice.jzqNo) ,
                   regular: 'zdNo',
                   length: 9,
                   placeHolder: '9位数字，如 000100001',
                   errorMsg: '集中器号：9位数字，取值规则：前4位[0001-9999]，后5位[00001-65535]',
                   limit: 'number'
                 }
               },
               {
                 classes:'allWidth',
                 type: 'complexInput',
                 label: '采集器号',
                 isEmpty: this.currentDevice.commPort == 31 ? true : false,
                 show: true,
                 option: [
                   {
                     width: '8.1em',
                     cs: 'server',
                     method: 'post',
                     param: {deviceType: this.deviceType, commPort: this.currentDevice.commPort},
                     rowName: 'data',
                     url: 'dictItem/queryCollectDevType',
                     text: 'dictItemName',
                     value: 'dictItemValue',
                     constant: this.currentDevice.collectDevType ? {dictItemValue:this.currentDevice.collectDevType }: '',
                     key: 'currentDevice.collectDevType'
                   },
                   {
                     type: 'normalInput',
                     key: 'currentDevice.cjqNo',
                     value:  this.currentDevice.cjqNo,
                     width: w,
                     regular: this.currentDevice.collectDevType == 2 ?  (this.currentDevice.commPort == 32 ? 'cjqDeviceNo' : 'otherDeviceNo') : 'otherDeviceNo' ,
                     placeHolder:  this.currentDevice.collectDevType == 2 ?  (this.currentDevice.commPort == 32 ? '13-16位数字和字母组合' : '1-12位数字和字母组合')  : '1-16位数字字母组合',
                     length: this.currentDevice.commPort == 32 ? 16 : 12,
                     errorMsg: this.currentDevice.collectDevType == 2 ? (this.currentDevice.commPort == 32 ? '转换器：13-16位数字和字母组合' : '转换器：1-12位数字和字母组合') : '节点：1-16位数字字母组合',
                     limit: 'moteEUI',
                   }
                 ]
               }
             ],
             'all': [
               
               {
                 classes: 'allWidth',
                 type: 'input',
                 show: true,
                 isEmpty: false,
                 label: '安装位置',
                 option: {
                   type: 'textArea',
                   value: this.currentDevice.installAddr || '',
                   placeHolder: '50个字符内',
                   length: 50,
                   width: widths,
                   key: 'currentDevice.installAddr'
                 }
               },
               {
                 classes: 'allWidth',
                 type: 'input',
                 show: true,
                 isEmpty: true,
                 label:  this.getEnumItem('deviceType', this.deviceType).text + '备注',
                 option: {
                   type: 'textArea',
                   value: this.currentDevice.remark || '',
                   width: widths,
                   key: 'currentDevice.remark'
                 }
               },
             ]
           }
         },
         RemianList () {
          let widths = 'calc(100% - 7em)';
          return [
             {
                 classes: 'halfWidth',
                 type: 'input',
                 isEmpty: false,
                 show: true,
                 label: '总示数',
                 option: {
                   type: 'normalInput',
                   value: this.currentDevice.readValue || 0,
                   length: 10,
                   placeHolder: '0-99999.9999之间任意数，允许4位小数',
                   width: widths,
                   dataType: '+float',
                   regular: 'float5',
                   errorMsg: '总示数： 0-99999.9999之间任意数，允许4位小数',
                   key: 'currentDevice.readValue'
                 }
               },
              {
                 classes: 'halfWidth hasMarginLi',
                 type: 'input',
                 isEmpty: false,
                 show: this.deviceType == '07' && parseInt(this.currentDevice.commFactorCnt) > 1,
                 label: '尖示数',
                 option: {
                   type: 'normalInput',
                   value: this.currentDevice.readValue1 || 0,
                   length: 10,
                   placeHolder: '0-99999.9999之间任意数，允许4位小数',
                   width: widths,
                   dataType: '+float',
                   regular: 'float5',
                   errorMsg: '尖示数：0-99999.9999之间任意数，允许4位小数',
                   key: 'currentDevice.readValue1'
                 }
              },
              {
                 classes: 'halfWidth',
                 type: 'input',
                 isEmpty: false,
                 show: this.deviceType == '07' && parseInt(this.currentDevice.commFactorCnt) > 1,
                 label: '峰示数',
                 option: {
                   type: 'normalInput',
                   value: this.currentDevice.readValue2 || 0,
                   length: 10,
                   placeHolder: '0-99999.9999之间任意数，允许4位小数',
                   width: widths,
                   dataType: '+float',
                   regular: 'float5',
                   errorMsg: '峰示数：0-99999.9999之间任意数，允许4位小数',
                   key: 'currentDevice.readValue2'
                 }
              },
              {
                 classes: 'halfWidth hasMarginLi',
                 type: 'input',
                 isEmpty: false,
                 show: this.deviceType == '07' && parseInt(this.currentDevice.commFactorCnt) > 1,
                 label: '平示数',
                 option: {
                   type: 'normalInput',
                   value: this.currentDevice.readValue3 || 0,
                   length: 10,
                   placeHolder: '0-99999.9999之间任意数，允许4位小数',
                   width: widths,
                   dataType: '+float',
                   regular: 'float5',
                   errorMsg: '平示数：0-99999.9999之间任意数，允许4位小数',
                   key: 'currentDevice.readValue3'
                 }
               },
               {
                 classes: 'halfWidth',
                 type: 'input',
                 isEmpty: false,
                 show: this.deviceType == '07' && parseInt(this.currentDevice.commFactorCnt) > 1,
                 label: '谷示数',
                 option: {
                   type: 'normalInput',
                   value: this.currentDevice.readValue4 || 0,
                   length: 10,
                   placeHolder: '0-99999.9999之间任意数，允许4位小数',
                   width: widths,
                   dataType: '+float',
                   regular: 'float5',
                   errorMsg: '谷示数： 0-99999.9999之间任意数，允许4位小数',
                   key: 'currentDevice.readValue4'
                 }
              }
          ]
         }
     },
     methods: {
       //提交数据时，对应的空字段校验
       emptyArr () {
         let arr = ['deviceNo',  'installAddr', 'meterUserNo'];
         switch (this.deviceType) {
           case '07': {
             let arrs;
              arrs = ['deviceNo',  'installAddr',  'ratio', 'meterUserNo'];
             if (this.currentDevice.commPort != 32) {
                arrs.unshift('jzqNo');
             }
             // if(this.currentDevice.offScheme == 1) {
             //  arrs.push('offParam');
            // }
             if(this.sign) {
                arrs.push('readValue');
                if(parseInt(this.currentDevice.commFactorCnt) > 1) {
                  arrs.push('readValue1');
                  arrs.push('readValue2');
                  arrs.push('readValue3');
                  arrs.push('readValue4');
               }
             }
             if(this.currentDevice.commPort == 32){
               arrs.unshift('cjqNo');
             }
             if(this.currentDevice.elecMeterCategory == 2 && false) {
                arrs.unshift('parentDeviceNo');
             }
             return arrs;
             break;
           }
           case '08':
           case '09': {
             let arrs = ['deviceNo', 'installAddr',  'meterUserNo'];
             if (this.currentDevice.commPort != 32) {
              arrs.push('jzqNo');
             }
             // if(this.currentDevice.offScheme == 1) {
             //  arrs.push('offParam')
             // }
             if(this.sign) {
               arrs.push('readValue');
             }
             if(this.currentDevice.commPort == 32) {
                arrs.push('cjqNo')
             }
             return arrs;
             break;
           }
            
         }
       },
       copyDevice(device) {
         let deviceInfo = this.getSearchData(this.deviceInfo);
         if (device) {
           deviceInfo = this.getSearchData(device)
         }
         this.currentDevice = this.extendObj(this.currentDevice, deviceInfo);
       },
       saveData() {
         let that = this;
         let arr = that.emptyArr();
         if(that.currentDevice.commPort == 32) {
             that.currentDevice.jzqNo = '999999999';
         } else if(that.currentDevice.commPort == 2) {
             that.currentDevice.cjqNo = '0';
         }
         that.regularTest({
             emptyKey: arr,
             prefix: 'currentDevice',
             callback: function () {
                that.currentDevice.deviceType = that.deviceType;
               // if(that.currentDevice.alarmThreshold < that.currentDevice.alarmThreshold1) {
               //       that.$refs.alerts.show({
               //             msg: '告警金额2应小于等于告警金额1！'
               //       });
               // } else {
                if(that.deviceList.length > 0) {
                  for (let i=0; i<that.deviceList.length;i++) {
                    if (that.deviceList[i].deviceNo === that.currentDevice.deviceNo && that.deviceType === that.deviceList[i].deviceType) {
                      that.$refs.alerts.show({
                        msg: '该设备已存在，不能重复添加！'
                      });
                      break;
                    }
                    else if (that.deviceList[i].meterUserNo  === that.currentDevice.meterUserNo && that.deviceType === that.deviceList[i].deviceType){
                      that.$refs.alerts.show({
                        msg: '表计户号已存在，请重新填写！'
                      });
                      break;
                    }
                  else if(i === (that.deviceList.length - 1)) {
                         if(typeof that.currentDevice.paramFlag == 'undefined' || that.currentDevice.paramFlag === '') {
                           that.$refs.alerts.show({
                              msg: '设备型号不能为空！'
                           });
                         } else {
                            if(that.deviceType == '07' && (parseInt(that.currentDevice.commFactorCnt) > 1) && that.sign) {
                               that.juedeMeterValue();
                            } else {
                               that.$emit('getNewDevice', {device: that.getSearchData(that.currentDevice), type: that.operate});
                               that.cancelDialog();
                            }
                         }
                         break;
                       }
                     }
                   } else {
                     if(that.deviceType == '07' && (parseInt(that.currentDevice.commFactorCnt) > 1) && that.sign) {
                         that.juedeMeterValue();
                    } else {
                     that.$emit('getNewDevice', {device: that.getSearchData(that.currentDevice), type: that.operate});
                     that.cancelDialog()
                   }
                 }
               //}
             }
           })
       },
       //判断尖峰平谷的值输入是否正确
       juedeMeterValue: function () {
         let that = this;
              // if((typeof that.currentDevice.readValue1 == 'undefined' || that.currentDevice.readValue1.toString() == '')
              //  && (typeof that.currentDevice.readValue2 == 'undefined'|| that.currentDevice.readValue2.toString() == '' )
              //  &&(typeof that.currentDevice.readValue3 == 'undefined'|| that.currentDevice.readValue3.toString() == '' )
              //  && (typeof that.currentDevice.readValue4 == 'undefined' || that.currentDevice.readValue4.toString() == '')) {
              //      that.$emit('getNewDevice', {device: that.getSearchData(that.currentDevice), type: that.operate});
              //      that.cancelDialog();
              // } else {
                  if((typeof that.currentDevice.readValue1 != 'undefined' && that.currentDevice.readValue1.toString() != '')
                      && (typeof that.currentDevice.readValue2 != 'undefined' && that.currentDevice.readValue2.toString() != '' )
                      && (typeof that.currentDevice.readValue3 != 'undefined'&& that.currentDevice.readValue3.toString() != '' )
                      && (typeof that.currentDevice.readValue4 != 'undefined' && that.currentDevice.readValue4.toString() != '')) {
                      let total = parseFloat(that.currentDevice.readValue.toString());
                      let top1 = parseFloat(that.currentDevice.readValue1.toString());
                      let top2 = parseFloat(that.currentDevice.readValue2.toString());
                      let top3 = parseFloat(that.currentDevice.readValue3.toString());
                      let top4 = parseFloat(that.currentDevice.readValue4.toString());
                      let c = parseFloat(Math.abs(total - (top1 + top2 +top3 +top4)).toFixed(4));

                      if(c == 0) {
                         that.$emit('getNewDevice', {device: that.getSearchData(that.currentDevice), type: that.operate});
                         that.cancelDialog();
                      } else if (c <= 0.02 && c > 0) {
                         that.$refs.confirms.show({
                           msg: '尖峰平谷之和与总示数之间存在 ' + c + ' 的偏差，确定保存吗？',
                           action: {
                              confirm: function () {
                                 that.$emit('getNewDevice', {device: that.getSearchData(that.currentDevice), type: that.operate});
                                 that.cancelDialog();
                              }
                           }
                         })
                      } else {
                         that.$refs.alerts.show({msg: '尖峰平谷之和与总示数之间偏差不能大于0.02！'});
                       }
                  } else  {
                     that.$refs.alerts.show({msg: '尖峰平谷都不能为空，且四个值的和等于总示数的值！'});
                  }

            //}
       },
       getForm(val) {
       
          if (val.key === 'currentDevice.paramFlag' && val !== '' && typeof val!== 'undefined') {
             let curr = val.currentSelect;
             this.currentDevice.commFactorCnt = curr.commFactorCnt;
          }
          if (this.deviceType !== '07' && val.key === 'currentDevice.collectDevType') {
            if (val.val == '1') {
                this.currentDevice['moteType'] =  'A';
            } else {
              this.currentDevice['moteType'] =  'C';
            }
          } else if (this.deviceType == '07' && val.key === 'currentDevice.commPort' && val.val != 32 && val.val!= 31) {
             this.currentDevice['collectDevType'] =  0;
          }
            this.getOption(val, this);
       },
       cancelDialog() {
           this.$refs.deviceShow.close();
           this.currentDevice = {
             moteType: '',
             deviceFactoryValue: '',
             localControl: 2,
             commPort:  32,
             readValue: 0,
             collectDevType:1,
            // offScheme: 0,
             commFactorCnt: '',
             elecType: '',
             elecMeterCategory: ''
           };
           this.showFrom = false
       },
       //flag: 代表是否表号可编辑
       show(type,flag, device) {
           this.showFrom = true;
           this.sign = type === 0 || (type === 1 && flag);
           this.operate = type;
           if (type === 1) {
             if (device) {
               this.copyDevice(device);
             } else {
               this.copyDevice()
             }
           } else {
             this.currentDevice = {
               localControl: 2,
               commPort: 32,
               jzqNo: '999999999',
               readValue: 0,
             //  initAmount: 0,
               deviceType: this.deviceType,
               ratio: 1,
             //  offScheme: 0,
               commFactorCnt: '',
               elecType: '',
               elecMeterCategory: ''
             };
             switch (this.deviceType) {
               case '07':
                 this.currentDevice.moteType = 'C';
                 break;
             }
           }
             let that = this;
             this.$nextTick(function () {
               that.$refs.deviceShow.show();
             })
         },
         testCJQ: function () {
         	let that = this;
          this.$nextTick(function () {
          	if(that.$refs['currentDevice.cjqNo'] && that.$refs['currentDevice.cjqNo'][0]) {
          		  that.$refs['currentDevice.cjqNo'][0].regularTest()
          	}
          });
         }
     },
     watch: {
         'currentDevice.localControl'(val) {
             if (val === 2) {
                 this.sign1 = false
             } else {
               this.sign1 = true
             }
             this.emptyArr();
         },
         'currentDevice.commPort'(val) {
           this.showDialog = false;
           let that = this;
           delete this.currentDevice.collectDevType;
           if (val == 2) {
             this.currentDevice.collectDevType = 1
           }
           this.$nextTick(function () {
             that.showDialog = true
           });
         },
        'currentDevice.collectDevType'() {
            this.testCJQ();
        }
     }
   }
</script>

<style lang="scss">

</style>



