<template>
  <div class="info-div" >
    <div class="info-header" style="top: 2%;left: 2em;">
      <span class="callback" @click="resetComponent">用户档案 </span>
      <span> > 添加档案</span>
    </div>
    <VuePerfectScrollbar class="info-container">
      <div class="list-content detail-content ">
        <div class="list-header">
          <i class="hasIcon hasImg"><img src="dist/img/file/user-message.png"></i>
          用户信息
          <button class="list-operate" @click="deviceSubmit"><i class="hasIcon">
            <icon name="save"></icon>
          </i>提交
          </button>
        </div>
        <ul class="form-list" style="margin-top:1em">
          <li v-for="(items, $indexs) in formOpt" v-if="!items.unShow" style="margin: 0.2em 0.5em;">
            <span v-if="!items.isEmpty" class="label-show " style="min-width:6em">
                    <span class="emptyTips">*</span>{{items.name}}&nbsp; &nbsp;</span>
            <span v-if="items.isEmpty" class="label-show " style="min-width:6em">
                          <span>&nbsp;&nbsp;</span>{{items.name}}&nbsp; &nbsp;</span>
            <inputs :isEmpty="items.isEmpty" :ref="items.option.key"
                    v-if="items.style == 'input' && items.option.type !== 'combineInput'" @getInput="getFrom"
                    :option="items.option"></inputs>
            <inputs :ref="items.option.key" v-if="items.style == 'input' && items.option.type === 'combineInput'"
                    :option="items.option" @editInput="editUserPhone"></inputs>
            <selects :ref="items.option.key" v-if="items.style == 'select'" @getSelect="getFrom"
                     :option="items.option"></selects>
          </li>
        </ul>
      </div>
      <div class="list-content detail-content " style="height:calc(100% - 18em)">
        <div class="list-header">
          <i class="hasIcon hasImg">
            <img src="dist/img/file/add-equipment-icon.png">
          </i>
          添加设备
        </div>
        <ul class="form-list" style="height:calc(100% - 2.5em)">
          <li style="width:calc(100% - 2em)">
            <span v-show="!noDataTips">您并没有设备！</span>
            <button v-for="(buttons, $btnIndex) in tabOpt" class="list-operate hasDeviceOpe"
                    :class="{ 'tabSelected': tabIndex == $btnIndex}"
                    @click="getTab(buttons, $btnIndex)">
              <i class="hasIcon">
                <!--<img src="dist/img/file/add-user-icon.png" />-->
                <icon name="plus-circle"></icon>
              </i>
              {{buttons.text}}
            </button>
          </li>
          <li style="width:calc(100% - 2em);height:calc(100% - 4em);margin-top:0">
            <table v-show="noDataTips" class="form-list table-list"
                   style="width:96%; font-size:0.875em;margin-top:0;margin-left:2%">
              <thead>
              <tr>
                <th style="width:5%">序号</th>
                <th style="width:12%">设备类型</th>
                <th style="width:13%">表号</th>
                <th style="width:13%">集中器号</th>
                <th style="width:14%">表计户号</th>
                <th style="width:23%">安装位置</th>
                <th style="width:20%">操作</th>
              </tr>
              </thead>
            </table>
            <VuePerfectScrollbar style="max-height: calc(100% - 4em);width:100%;position: relative;top: -0.3em;width: 96%;margin-left:2%">
              <table v-show="noDataTips" class="form-list table-list"
                     style=" font-size:0.875em;margin-top: 0;border-top: 0; width: 100%;margin:0">
                <tbody>
                <tr v-for="(deviceInfo, $index) in deviceData">
                  <td style="width:5%">{{$index + 1}}</td>
                  <td style="width:12%">{{getTabText(deviceInfo.deviceType)}}</td>
                  <td style="width:13%">{{deviceInfo.deviceNo}}</td>
                  <td style="width:13%">{{deviceInfo.jzqNo}}</td>
                  <td style="width:14%">{{deviceInfo.meterUserNo}}</td>
                  <td style="width:23%">{{deviceInfo.installAddr}}</td>
                  <td style="width:20%">
                    <button class="list-operate" style="margin-top:2px; height: 2em;" @click="removeDevice($index)">
                      <i class="hasIcon">
                        <icon name="trash"></icon>
                      </i>删除
                    </button>
                    |
                    <button class="list-operate" style="margin-top:2px;height: 2em;" @click="editDevice($index)">
                      <i class="hasIcon">
                        <icon name="edit"></icon>
                      </i>修改
                    </button>
                  </td>
                </tr>
                </tbody>
              </table>
            </VuePerfectScrollbar>
          </li>
        </ul>
      </div>
      <operateDevice v-if="showOperate" :deviceType="currentDeviceType" :deviceInfo="deviceInfo" ref="operateDevices"
                     @getNewDevice="copyNewDeviceType" :deviceList="sendChildDevices"></operateDevice>
    </VuePerfectScrollbar>
    <!--客户电话号码-->
    <msgPhone v-if="showCstPhone" ref="editCstPhones" optType="add" :customerInfo="customerInfo"
              @getUserPhone="getUserPhone"></msgPhone>
  </div>
</template>
<script>
  import operateDevice from '../../childComponent/deviceOperate.vue'
  import msgPhone from '../../files/staff/MsgPhones.vue'
  import 'vue-awesome/icons/plus-circle'
  export default {
    data() {
      return {
        showCstPhone: false,
        sendChildDevices: [],
        currentIndex: 0, //当前选择编辑的设备下标
        clickEvent: true, //避免单击事件触发多次
        noDataTips: false,
        tabIndex: -1, //设备类型下标
        deviceData: [], //设备集合
        customerInfo: {
          customerBrand: '',
          customerName: '',
          customerContact: '',
          mobilePhones: "",
          customerAddr: '',
          customerNo: '',
          remark: '',
          buildName: '',
          orgNo: '',
          buildNo: '',
          initAmount: 0,
          overdraftAmount: '',
          alarmThreshold: 0,
          alarmThreshold1:0,
          alarmThreshold2: 0,
          offScheme: ''
        }, //用户基础信息
        deviceInfo: {}, //编辑设备时传递给子组件的设备信息
        showOperate: false,  //重新渲染设备弹框
        tabOpt: [     //设备类型结合
          {text: '电表', deviceType: '07'},
          {text: '水表', deviceType: '08'},
          {text: '气表', deviceType: '09'}
        ],
        currentDeviceType: '07'
      }
    },
    methods: {
      getUserPhone: function (val) {
        
           let arr = [];
           if(typeof val === 'object') {
            for (let i = 0; i < val.length; i++) {
              arr.push(val[i].phone);
           }
           this.$refs['customerInfo.customerContact'][0].resetSearch(arr.join(','))
           this.customerInfo.mobilePhones = arr.join(',');
           }
      },
      //新增/修改客户电话
      editUserPhone: function () {
        let that = this;
        that.showCstPhone = true;
        that.$nextTick(function () {
          that.$refs.editCstPhones.show();
        })
      },

      getFrom: function (val) {
        if(val.key === 'customerInfo.buildNo') {
          this.customerInfo.buildName = val.currentSelect.dictItemName;
        }
        this.getOption(val, this);
      },
      //返回档案列表
      resetComponent: function () {
        let that = this;
        that.$emit('closeDiv', true);
      },
      //提交用户档案的数据
      deviceSubmit: function () {

        let that = this;
        let loadings = this.loadings;
        let alerts = this.alerts;

        if (that.clickEvent) {
          that.clickEvent = false;
          if (this.customerInfo.mobilePhones == "") {
            alerts.show({msg: '请输入电话号码！'})
            that.clickEvent = true;
            return;
          }
          else {
            this.customerInfo.customerContact = this.customerInfo.mobilePhones.split(',')[0];
          }
          let notEmpty =  ['customerName', 'customerAddr', 'orgNo', 'propertyName', 'initAmount', 'buildNo', 'alarmThreshold','alarmThreshold1','alarmThreshold2']
          if(this.customerInfo.offScheme == 1) {
             notEmpty.push('offParam')
          }
          that.regularTest({
            emptyKey: notEmpty,
            prefix: 'customerInfo',
            callback: function () {
              if (parseFloat(that.customerInfo.alarmThreshold2) > parseFloat(that.customerInfo.alarmThreshold1) || parseFloat(that.customerInfo.alarmThreshold1) > parseFloat(that.customerInfo.alarmThreshold) || parseFloat(that.customerInfo.alarmThreshold2) > parseFloat(that.customerInfo.alarmThreshold)) {
                alerts.show({msg: '告警阀值之间的大小关系应为（告警阀值1 >= 告警阀值2 >= 告警阀值3）'})
              } else if (that.deviceData.length === 0) {
                   alerts.show({msg: '请至少添加一种设备！'})
              } else {
                loadings.show({
                  msg: '正在提交资料,请稍后..'
                });
                let submitData = {
                  customerInfo: that.customerInfo,
                  electricMeter: [],
                  waterMeter: [],
                  gasMeter: []
                };
                for (let i = 0; i < that.deviceData.length; i++) {
                  let device = that.deviceData[i];
                  switch (device.deviceType) {
                    case '07':
                      submitData.electricMeter.push(device);
                      //   submitData.electricMeter.moteType = 'C';
                      break;
                    case '08':
                      submitData.waterMeter.push(device);
                      //      submitData.waterMeter.meterType = 0;
                      break;
                    case '09':
                      submitData.gasMeter.push(device);
                      //     submitData.gasMeter.meterType = 0;
                      break;
                  }
                }
                that.getRequest({
                  url: 'customerInfo/add',
                  method: 'post',
                  param: submitData,
                  success: function (data) {
                    loadings.close();
                    alerts.show({
                      msg: data.message
                    });
                    that.$emit('closeDiv', true);
                    //提交保存数据 //data.data= cusomterNo
                    that.$refs.editCstPhones.submitCstPhoneSave(data.data,that.$refs.editCstPhones.cstPhones.mobilePhones);
                  },
                  error: function (data) {
                    loadings.close();
                    alerts.show({
                      msg: data.message
                    });
                  }
                })
              }
            }
          });
          setTimeout(function () {
            that.clickEvent = true
          }, 500)
        }
      },
      //移除添加的设备
      removeDevice: function (index) {
        this.deviceData.splice(index, 1);
        if (this.deviceData.length === 0) {
          this.noDataTips = false;
        }
      },
      //编辑添加好的设备
      editDevice: function (index) {
        this.sendChildDevices = [];
        this.deviceInfo = this.deviceData[index];
        let len = this.deviceData.length;
        for (let i = 0; i < len; i++) {
          if (i !== index) {
            this.sendChildDevices.push(this.deviceData[i]);
          }
        }
        this.currentIndex = index;
        this.currentDeviceType = this.deviceInfo.deviceType;
        this.operateDevice(1)
      },
      //切换设备类型按钮触发对应的弹出事件
      getTab: function (val, index) {
        let type = val.deviceType;
        this.tabIndex = index;
        this.currentDeviceType = type;
        this.sendChildDevices = this.deviceData;
        this.operateDevice(0);
      },
      //获取当前点击设备的设备类型名称
      getTabText: function (deviceType) {
        let deviceText;
        switch (deviceType) {
          case '07':
            deviceText = '电表';
            break;
          case '08':
            deviceText = '水表';
            break;
          case '09':
            deviceText = '气表';
            break;
        }
        return deviceText
      },
      //显示设备的弹出框 type:0-添加 1-编辑
      operateDevice: function (type) {
        let that = this;
        that.showOperate = true;
        setTimeout(function () {
          //第二个参数代表表号是否可操作
          that.$refs.operateDevices.show(type, true);
        }, 0)
      },
      //接收弹出框（子组件）传递过来的设备值
      //val.type: 0-添加 1-编辑 val.device :设备信息
      copyNewDeviceType: function (val) {
        this.noDataTips = true;
        switch (val.type) {
          case 0:
            this.deviceData.splice(0, 0, val.device);
            break;
          case 1:

            this.deviceData.splice(this.currentIndex, 1, val.device);

            break;
        }
      }
    },
    components: {
      operateDevice,
      msgPhone
    },
    props: ['buildNo', 'departNo'],
    computed: {
      //获取设备类型
      deviceType: function () {
        return this.tabOpt[this.tabIndex].deviceType
      },
      //用户基础资料显示
      formOpt: function () {
     
        let width = 'calc(100% - 6.5em)';
        return [
          {
            name: '用户姓名',
            style: 'input',
            classes: 'halfWidth',
            isEmpty: false,
            option: {
              key: 'customerInfo.customerName',
              width: width,
              value: '',
              type: 'normalInput',
              regular: '',
              length: 20,
              placeHolder: '请输入用户姓名',
              errorMsg: '任意字符'
            }
          },
          {
            name: '联系电话',
            style: 'input',
            isEmpty: false,
            classes: 'halfWidth',
            option: {
              key: 'customerInfo.customerContact',
              width: width,
              value: this.customerInfo.mobilePhones,
              type: 'combineInput'
            }
          },
          {
            name: '用户地址',
            style: 'input',
            classes: 'halfWidth',
            isEmpty: false,
            option: {
              key: 'customerInfo.customerAddr',
              value: '',
              width: width,
              length: 50,
              placeHolder: '50个字符内',
              type: 'normalInput'
            }
          },
          {
            name: '门牌编号',
            style: 'input',
            classes: 'halfWidth',
            isEmpty: false,
            option: {
              key: 'customerInfo.propertyName',
              value: '',
              width: width,
              length: 20,
              placeHolder: '可输入字母数字、下划线、-',
              type: 'normalInput',
              errorMsg: '格式：字母、数字、下划线、-',
              limit: 'NCO'
            }
          },
          {
            name: '商家品牌',
            style: 'input',
            classes: 'halfWidth',
            isEmpty: true,
            option: {
              key: 'customerInfo.customerBrand',
              value: '',
              width:width,
              type: 'normalInput',
              length: 50,
              placeHolder: '50个字符内',
            }
          },
          {
            name: '部门机构',
            style: 'select',
            classes: 'halfWidth',
            isEmpty: false,
            option: {
              type: 'complexSelect',
              key: 'customerInfo.orgNo',
              cs: 'server',
              url: 'org/queryOrgTreeByOrgNo',
              constant: this.departNo && this.departNo != '' ? {orgNo: this.departNo}: '' ,
              method: 'post',
              noCheck: {key: 'leaf', value: 0}, 
              width: width,
              text: 'orgName',
              value: 'orgNo',
              rowName: 'data'
            }
          },
          {
            name: '楼栋编号',
            style: 'select',
            isEmpty: false,
            option: {
               width: width,
               cs: 'server',
               method: 'post',
               url: 'dictItem/queryByDictCode',
               param: {dictCode: '15'},
               // method: 'get',
               // url: 'dist/json/build.json',
               rowName: 'data',
               text: 'dictItemName',
               value: 'dictItemValue',
               constant: this.buildNo && this.buildNo != '' ? {dictItemValue:this.buildNo}: '' ,
               key: 'customerInfo.buildNo'
            }
          },
          
          {
            name: '告警阀值1',
            style: 'input',
            isEmpty: false,
            option: {
               width: width,
               type: 'normalInput',
               value: 0,
               dataType: '+float',
               placeHolder: '0-99999.99之间任意数，允许两位小数',
               key: 'customerInfo.alarmThreshold',
               length: 8,
               regular: 'recharges',
               errorMsg: '告警阀值1：0-99999.99之间任意数，允许两位小数',
               //limit: 'number'
            }
          },
          {
            name: '告警阀值2',
            style: 'input',
            isEmpty: false,
            option: {
               width: width,
               type: 'normalInput',
               value: 0,
               dataType: '+float',
               placeHolder: '0-99999.99之间任意数，允许两位小数',
               key: 'customerInfo.alarmThreshold1',
               length: 8,
               regular: 'recharges',
               errorMsg: '告警阀值2：0-99999.99之间任意数，允许两位小数',
               //limit: 'number'
            }
          },
          {
            name: '告警阀值3',
            style: 'input',
            isEmpty: false,
            option: {
               width: width,
               type: 'normalInput',
               value: 0,
               dataType: '+float',
               placeHolder: '0-99999.99之间任意数，允许两位小数',
               key: 'customerInfo.alarmThreshold2',
               length: 8,
               regular: 'recharges',
               errorMsg: '告警阀值3：0-99999.99之间任意数，允许两位小数',
               //limit: 'number'
            }
          },
          {
            name: '预留金额',
            style: 'input',
            isEmpty: false,
            option: {
              key: 'customerInfo.initAmount',
              width:width,
              type: 'normalInput',
              dataType: '+float',
              placeHolder: '预留金额：0-1000之间任意数字,允许两位小数',
              length: 6,
              value: 0,
              regular: 'recharge',
              errorMsg: '预留金额：0-1000之间任意数字,允许两位小数',
              limit: 'float'
            }
          },
          {
            name: '透支金额',
            style: 'input',
         
            isEmpty: true,
            option: {
              key: 'customerInfo.overdraftAmount',
              width:width,
              type: 'normalInput',
              dataType: 'int',
              placeHolder: '0-999999之间任意整数',
              length: 6,
             
              errorMsg: '透支金额：0-999999之间任意整数',
              limit: 'number'
            }
          },
          {
              style: 'select',
              isEmpty: false,
              name: '关断策略',
              option: {
                  width: width,
                  cs: 'client',
                  data: this.getEnumItem('offScheme').obj,
                  constant: this.customerInfo.offScheme!=='' ? {value:this.customerInfo.offScheme }: '' ,
                  key: 'customerInfo.offScheme'
              }
           },
           {
              style: 'input',
              unShow: this.customerInfo.offScheme != 1,
              isEmpty: false,
              name: '延时时长',
              option: {
                   type: 'normalInput',
                   value: this.customerInfo.offParam || 1,
                   placeHolder: '小时为单位，1-2位整数，最小为1',
                   errorMsg: '小时为单位，1-2位整数，最小为1',
                   length: 2,
                   limit: 'number',
                   regular: 'offParam',
                   width: width,
                   key: 'customerInfo.offParam'
              }
          },
          {
            name: '备注信息',
            classes: 'allWidth',
            isEmpty: true,
            style: 'input',
            option: {
              key: 'customerInfo.remark',
              width: width,
              placeHolder: '200个字符内',
              length: 200,
              value: '',
              type: 'normalInput'
            }
          }
        ]
      },
      //获取父组件的提示框
      alerts: function () {
        return this.$parent.$refs.alerts;
      },
      //获取父组件的加载框
      loadings: function () {
        return this.$parent.$refs.loadings;
      }
    }
  }
</script>
<style lang="scss">
.form-list .tabSelected {
    background: transparent !important;
    color: white;
    outline: none;
  }

  .hasDeviceOpe {
    font-size: 0.875em;
  }
</style>



